// location-header
.location-header {
    background-image: url(../img/72.png);
    height: 304px;
    .location-header-title {
        font-size: 64px;
        line-height: 1.5;
        margin-right: 10px;
    }
}

// location-header rwd
@include pad() {
    .location-header {
        .location-header-title {
            font-size: 48px;
            margin-right: 16px;
        }
        .question-icon{
            width: 48px;
            height: 48px;
        }
    }
}
@include mobile() {
   .location-header {
        height: 143px;
        .location-header-title {
            font-size: 28px;
            margin-right: 8px;
        }
        .question-icon{
            width: 24px;
            height: 24px;
        }
    }
}

// location section
.location-section {
    padding-top: 22px;
    padding-bottom: 156px;
    .selection-wrap {
        width: 32%;
        margin-bottom: 32px;
        .selection-title {
            font-size: 24px;
        }
        select {
            border: 1px solid #000;
            width: 69.27%;
            padding: 10px 19px;
            outline: 0;
            font-size: 18px;
            font-family: $font-style;
        }
    }
    .sidebar {
        width: 32%;
        border: 1px solid #00000020;
        .sidebar-body {
            .sidebar-title{
                font-size: 24px;
                padding: 16px 24px;
                border-bottom: 1px solid #00000020;
            }
            .sidebar-body-list {
                padding: 16px 37px;
                .siedbar-body-item {
                    .sidebar-tel {
                        display: inline-block;
                        &:hover {
                            text-decoration: underline;
                            color: gray;
                        }
                    }
                    .icon-wrap {
                        width: 26px;
                        text-align: center;
                        display: flex;
                        justify-content: center;
                        margin-right: 8px;
                    }
                }
            }
        }
        .btn-information {
            text-align: center;
            color: #fff;
            border-top: 1px solid #00000020;
            background: #000;
            font-size: 20px;
            padding-top: 20px;
            padding-bottom: 20px;
            &:hover {
                background: #545454;
            }
        }
    }
    .map-img {
        width: 66%;
    }
}

// location section rwd
@include pad (){
    .location-section {
        padding-top: 8px;
        padding-bottom: 104px;
        .selection-wrap {
            width: 48.21%;
            margin-bottom: 24px;
            .selection-title {
                font-size: 16px;
            }
            select {   
                width: 73.15%;
                padding: 12px 16px;
                font-size: 16px;
            }
        }
        .sidebar {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border: none;
            margin-bottom: 32px;
            .sidebar-header {
                width: 48.21%;
                img {
                    width: 100%;
                }
            }
            .sidebar-body {
                width: 48.21%;
                border: 1px solid #00000021;
                .sidebar-title{
                    font-size: 16px;
                    padding: 8px 16px;
                }
                .sidebar-body-list {
                    padding: 16px 19px;
                    .siedbar-body-item {
                        .icon-wrap {
                            width: 21px;
                        }
                    }
                }
            }
            .btn-information {
                display: none;
            }
        }
        .map-img {
            width: 100%;
        }
    }
}

@include mobile (){
    .location-section {
        padding-bottom: 56px;
        .selection-wrap {
            width: 100%;
            margin-bottom: 20px;
            .selection-title {
                font-size: 14px;
            }
            select {   
                width: 79.55%;
                padding: 8px 16px;
                font-size: 14px;
            }
        }
        .sidebar {
            flex-direction: column;
            margin-bottom: 20px;
            .sidebar-header {
                width: 100%;
            }
            .sidebar-body {
                width: 100%;
                .sidebar-body-list {
                    padding: 16px;
                    .siedbar-body-item {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}