// classic 經典系列鏡框 
.classic {
    padding-top: 64px;
    padding-bottom: 80px;
    .classic-list {
        .classic-subtitle {
            font-size: 48px;
            color: #AA0601;
            text-transform: uppercase;
            font-style: italic;
            font-weight: 900;
            width: 48%;
        }
        .classic-img {
            width: 66%;
        }
        li:nth-child(2) img {
            width: 100%;
        } 
        .p-img {
            position: relative;
            &::before {
                content: '';
                background-image: url(../img/15.png);
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                opacity: 0;
                background-size: contain;
            }
            &:hover::before {
                opacity: 1;
            }
        }

    }
}

@include pad() {
    .classic {
        padding-top: 40px;
        padding-bottom: 40px;
        .classic-title {
            margin-bottom: 32px;
        }
        .classic-list {
            .classic-list-item{
                margin-bottom: 8px;
                img,
                .p-img {
                    width: 50%;
                    margin-right: 0;
                }
                .classic-subtitle {
                    font-size: 28px;
                    justify-content: center;
                    width: 50%;
                    .classic-diamond-icon {
                        width: 32px;
                        height: 25.16px;
                    }
                    .classic-sun-icon {
                        width: 32px;
                        height: 32px;
                    }
                    .classic-lightning-icon {
                        width: 23px;
                        height: 32px;
                    }
                }
            }
            & li:nth-child(2) {
                flex-direction: row-reverse;
                justify-content: flex-end;
                h3 {
                    margin-right: 0;
                }
            }

        }
    }
}
@include mobile(){
    .classic {
        padding-top: 24px;
        padding-bottom: 24px;
        .classic-title {
            margin-bottom: 16px;
        }
        .classic-list {
            .classic-list-item {
                position: relative;
                img,
                .p-img {
                    width: 100%;
                }
                .classic-subtitle {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 100;
                    white-space: nowrap;
                    color: #fff;
                    .classic-diamond-icon,
                    .classic-sun-icon, 
                    .classic-lightning-icon {
                       filter: brightness(500);
                    }
                }
            }
        }
    }
}

// crossover 聯名設計鏡框
/* crossover */
.crossover {
    padding-top: 64px;
    padding-bottom: 479px;
    background-image: url(../img/nick-owuor-astro-nic-visuals-54Q_HvMWtVE-unsplash.png);
    .crossover-img-box {
        /* max-width: 1539px; */
        margin: 0 auto 60px;
        .crossover-flex {
            margin-left: -170px;
            margin-right: -170px;
            .crossover-img-1,
            .crossover-img-2,
            .crossover-img-3,
            .crossover-img-4{
                display: block;
            }
            .crossover-img-1 {
                width: 37.23%;
            }
            /* 右側大半區塊 */
            .crossover-width-wrap-l {
                width: 62.25%;
                .crossover-img-3 {
                    width: 13.78%;
                }
                /* 右側下半區塊的右邊區塊 */
                .crossover-width-warp-m {
                    width: 85.39%;
                    /* mobile rwd 替換圖 */
                    .crossover-img-mobile {
                        display: none;
                    }
                    /* btn more */
                    .btn-more{
                        position: relative;
                        height: 100%;
                        display: flex;
                        max-width: 31.66%;
                        transition: all .65s .3s;
                        transform: translateX(0px);
                        margin-right: 17.5%;
                        &::after {
                            content: "";
                            position: absolute;
                            background: white;
                            top: 8%;
                            bottom: 8%;
                            left: 10%;
                            right: -55%;
                        }
                        span {
                            padding: 5px 0 5px 32px;
                            font-size: 72px;
                            color: #AA0601;
                            font-weight: bold;
                            position: absolute;
                            top: 50%;
                            left: 60%;
                            transform: translate(-50%, -50%);
                            z-index: 10000;
                            white-space: nowrap; 
                            &::before {
                                content: 'MORE';
                                position: absolute;
                                transition: all .65s .3s;
                                transform: translateY(0px);
                            }
                            &:hover::before {
                                transform: translateY(-85px);
                                opacity: .7;
                            }
                            &::after {
                                content: 'MORE';
                                position: absolute;
                                transition: all .65s .3s;
                                transform: translateY(0px);
                            }
                            &:hover::after {
                                transform: translateY(-145px);
                                opacity: .4;
                            }
                        }
                        &:hover img:last-child {
                            transform: translateX(15px);
                        }
                    }
                }
            }
        }
    }
    
    
    
    
    
    /* Double A+ YOUTH */
    .crossover-brand-box {
        position: relative;
        width: 48.75%;
    }
    .crossover-brand-box::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(225, 225, 225,0.5 );
    }
    .brandName {
        font-size: 72px;
        color: #AA0601;
        font-weight: bold;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 100;
        white-space: nowrap;
    }
}


@include pc1539(){
    .crossover {
        .crossover-img-box {
            .crossover-flex {
                margin-left: 0;
                margin-right: 0;
            }
        }
    }
}
@include pad(){
    .crossover {
        padding-top: 40px;
        padding-bottom: 417px;
        .crossover-titel {
            margin-bottom: 32px;
        }
        /* crossover-img-box */
        .crossover-img-box {
            margin-bottom: 40px;
            .crossover-flex {
                .crossover-width-wrap-l{
                    .crossover-width-warp-m {
                        .btn-more {
                            span {
                                font-size: 40px;
                                left: 55%;
                                &:hover::before {
                                    transform: translateY(-45px);
                                }
                                &:hover::after {
                                    transform: translateY(-80px);
                                }
                                .btn-more-arrow {
