/* contact */
.contact {
    background: #aa06010d;
    padding-top: 64px;
    padding-bottom: 92px;
    .contact-form {
        width: 64.71%;
        .contact-form-text {
            font-size: 20px;
            margin-bottom: 20px;
        }
        label{
            display: block;
            font-size: 16px;
            margin-bottom: 4px;
        }
        .name,
        .phone,
        .email,
        .comment {
            display: block;
            background: transparent;
            border: 1px solid #000;
            padding: 14px 0 14px 16px;
            width: 100%;
            font-family: $font-style;
            outline: 0;
        }
        .comment {
            resize: none;
        }
        // placeholder字體顏色
        ::-webkit-input-placeholder { /* Edge */     
            color: rgba(0, 0, 0, 0.2)
        }
        :-ms-input-placeholder { /* Internet Explorer */
            color: rgba(0, 0, 0, 0.2)
        }
        ::placeholder {
            color: rgba(0, 0, 0, 0.2)
        }
        
        /* checkbox */
        .contact-checkbox {
            font-size: 16px;
            display: block;
            position: relative;
            padding-left: 30px;
            margin-bottom: 12px;
            cursor: pointer;
            margin-bottom: 40px;
        
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            /* Hide the browser's default checkbox */
            input {
                position: absolute;
                opacity: 0;
                cursor: pointer;
                height: 0;
                width: 0;
            }
            /* Create a custom checkbox */
            .checkmark {
                position: absolute;
                top: 0;
                left: 0;
                height: 22px;
                width: 22px;
                background-color: transparent;
                border: 2px solid #000;
                border-radius: 2px;
                /* Create the checkmark/indicator (hidden when not checked) */
                &:after {
                    content: "";
                    position: absolute;
                    display: none;

                    /* Style the checkmark/indicator */
                    left: 6px;
                    top: 2px;
                    width: 7px;
                    height: 11px;
                    border: solid #000;
                    border-width: 0 3px 3px 0;
                    -webkit-transform: rotate(45deg);
                    -ms-transform: rotate(45deg);
                    transform: rotate(45deg);
                }
            }
            /* On mouse-over, add a grey background color */
            &:hover input ~ .checkmark {
                background-color: #ccc;
            }
            /* Show the checkmark when checked */
            input:checked ~ .checkmark::after {
                  display: block;
            }
        }
        /* check box end */
        
        .btn-submit {
            display: block;
            width: 180px;
            height: 54px;
            background: #AA0601;
            font-size: 16px;
            color: #fff;
            margin: 0 auto;
            cursor: pointer;
            border: none;
            font-family: $font-style;
            &:hover {
                background: #7d0502;
            }
        }
    }
}

@include pad(){
    .contact {
        padding-top: 40px;
        padding-bottom: 56px;
        .contact-title {
            margin-bottom: 16px;
        }
        .contact-form {
            width: 74.11%;
            .contact-form-text {
                margin-bottom: 16px;
            }
            .contact-checkbox {
                margin-bottom: 24px;
            }
        }
    }
}
