.nav {
    background: #AA0601;
    color: #fff;
    padding: 30px 88px;
    font-size: 20px;
    .nav-hamburger {
        display: none;
    }
    .nav-list-item {
        margin-right: 4rem;
        &:last-child {
            margin-right: 0;
        }
    }

}
@include pad(){
    .nav {
        padding: 30px 12px 30px 48px;
        .nav-list-item {
            margin-right: 2.5rem;
        }
    }
}

@include mobile(){
    .nav {
        padding: 10px 0;
        position: relative;
        .nav-flex {
            justify-content: center;
        }
        .nav-hamburger {
            display: block;
            position: absolute;
            left: 16px;
        }
        .nav-dropdown {
            display: block;
            position: absolute;
            left: 0;
            top: 100%;
            width: 100%;
            max-height: 0;
            transition: all 0.8s;
            overflow: hidden;
            z-index: 120;
            &.active {
                max-height: 252px;
            }
        }
        .nav-list-item {
            margin-right: 0;
        }
        .nav-list-item a {
            background: #AA0601;
            color: #FFF;
            border-top: 1px solid #FFF;
            padding: 16px 0;
            text-align: center;
        }
        .nav-list-item a:active {
            background: #8f0400;
        }
    }
}